import Vue from 'vue'
import Router from 'vue-router'
import HomeRu from '@/components/ru/HomeRu'
import HomeEn from '@/components/en/HomeEn'
import SetProjects from '@/components/SetProjects'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HomeRu',
      component: HomeRu
    },
    {
      path: '/en',
      name: 'HomeEn',
      component: HomeEn,
      beforeEnter: (to, from, next) => {
         next()
      }
    },
    {
      path: '/setproject',
      name: 'SetProjects',
      component: SetProjects,
      beforeEnter: (to, from, next) => {
         next()
      }
    },


    // {
    //   path: '/chat',
    //   name: 'Chat',
    //   component: Chat,
    //   props: true,
    //   beforeEnter: (to, from, next) => {
    //     if (to.params.name) {
    //       next()
    //     } else {
    //       next({ name: 'Welcome'})
    //     }
    //   }
    // }
  ]
})



