<template lang="html">
  <div class="container pb-5 pt-3">
    <div class="">
        <a v-on:click="returnHome"><i class="arrow fas fa-arrow-left mr-4"></i> Вернуться на главную</a>
        <div class="" id="top"></div>
    </div>
    <Title class="pt-5" txt="Добавление Проекта"/>
    <div class="">
      Выберете язык вашего проекта
      <btn color="outline-indigo">Русский</btn>
      <btn color="outline-indigo">English</btn>
    </div>
    <div class="form-group pt-5">
      <row class="row mb-4">
        <div class="col-lg-4">
          <label for="#nameProject">Наименование вашего проекта</label>
          <input type="text" class="form-control" id="nameProject" name="" value="">
        </div>
      </row>
      <row>
        <div class="col-lg-6 ">
          <p>Ваш логотип</p>
          <btn color="outline-indigo" class="mb-3">Загрузить</btn>
          <card class="pt-3 pl-3 pb-3 pr-3 logotip__container_set col-lg-10">
            <img style="width: 200px;" src="../assets/logo.png" alt="">
          </card>
        </div>
        <div class="col-lg-6">
          <p>Отрасли* (можно выбрать несколько отраслей)</p>
          <dropdown class="dropdown__otrasli" v-on:click="toggle = !toggle">
            <dropdown-toggle color="outline-indigo"  slot="toggle">Отрасли</dropdown-toggle>
            <dropdown-menu>
              <dropdown-item>Action</dropdown-item>
              <dropdown-item>Another action</dropdown-item>
              <dropdown-item>Something else here</dropdown-item>
            </dropdown-menu>
          </dropdown>
        </div>
      </row>
      <div class="container text-center pt-4">
        <h2>Географическое расположение реализации проекта</h2>
      </div>
      <row class="pt-5">

        <div class="col-lg-3 pt-4">
          <dropdown class="dropdown__otrasli" v-on:click="toggle = !toggle">
            <dropdown-toggle color="outline-indigo"  slot="toggle">Страна</dropdown-toggle>
            <dropdown-menu>
              <dropdown-item>Action</dropdown-item>
              <dropdown-item>Another action</dropdown-item>
              <dropdown-item>Something else here</dropdown-item>
            </dropdown-menu>
          </dropdown>
        </div>
        <div class="col-lg-4">
          <label for="#region_set">Регион</label>
          <input type="text" class="form-control" id="region_set" name="" value="">
        </div>
        <div class="col-lg-4">
          <label for="#city_set">Город</label>
          <input type="text" class="form-control" id="city_set" name="" value="">
        </div>

      </row>
      <row class="pt-3">
        <div class="col-lg-3 pt-4">
          <dropdown class="dropdown__otrasli" v-on:click="toggle = !toggle">
            <dropdown-toggle color="outline-indigo"  slot="toggle">Наличие патента</dropdown-toggle>
            <dropdown-menu>
              <dropdown-item>Да</dropdown-item>
              <dropdown-item>Нет</dropdown-item>
            </dropdown-menu>
          </dropdown>
        </div>
        <div class="col-lg-4">
          <label for="#satd">Стадия реализации проекта</label>
          <input type="text" class="form-control" id="stad" name="" value="">
        </div>

        <div class="col-lg-4">
          <label for="#description_project">Описание бизнес-проекта* (не менее 150 символов)</label>
          <textarea name="name" rows="6" cols="30"></textarea>
        </div>
      </row>

      <row class="pt-3">
        <div class="col-lg-3 pt-5">
          <dropdown class="dropdown__otrasli" v-on:click="toggle = !toggle">
            <dropdown-toggle color="outline-indigo"  slot="toggle">Условия привлечения инвестирования</dropdown-toggle>
            <dropdown-menu>
              <dropdown-item>Грант</dropdown-item>
              <dropdown-item>Кредит</dropdown-item>
              <dropdown-item>Долевое участие</dropdown-item>
            </dropdown-menu>
          </dropdown>
        </div>
        <div class="col-lg-4">
          <label>Валюта</label>
          <row class="pt-2">
            <btn color="outline-indigo">Рубль(RUB)</btn>
            <btn color="outline-indigo">Доллар(USD)</btn>
            <btn color="outline-indigo"> Евро(EUR)</btn>
            <btn color="outline-indigo">BTK</btn>
            <btn color="outline-indigo">ETH</btn>
          </row>
        </div>
        <div class="col-lg-4 ">
          <label for="#volume-project">Общая стоимость проекта*</label>
          <input type="text" class="form-control mt-2" id="volume-project" name="" value="">

        </div>
      </row>
      <row class="pt-3">
        <div class="col-lg-4">
          <label for="#volume-invest">Объем инвестиций*</label>
          <input type="text" class="form-control mt-2" id="volume-invest" name="" value="">
        </div>
        <div class="col-lg-4">
          <label for="#volume-invest">Кому доступны контакты для просмотра?*</label>
          <dropdown class="dropdown__otrasli" v-on:click="toggle = !toggle">
            <dropdown-toggle color="outline-indigo"  slot="toggle">Всем</dropdown-toggle>
            <dropdown-menu>
              <dropdown-item>Всем</dropdown-item>
              <dropdown-item>Только зарегистрированным пользователям</dropdown-item>
            </dropdown-menu>
          </dropdown>
        </div>
        <div class="col-lg-4">
          <label for="#volume-invest">Скрыть страницу проекта от поисковых систем?</label>
          <dropdown class="dropdown__otrasli" v-on:click="toggle = !toggle">
            <dropdown-toggle color="outline-indigo"  slot="toggle">Нет</dropdown-toggle>
            <dropdown-menu>
              <dropdown-item>Да</dropdown-item>
              <dropdown-item>Нет</dropdown-item>
            </dropdown-menu>
          </dropdown>
        </div>
      </row>
      <row class="pt-3">
        <div class="col-lg-4">
          <label for="#project-site">Сайт компании*</label>
          <input type="text" class="form-control mt-2" id="project5-site" name="" value="">
        </div>
        <div class="col-lg-4">
          <label for="#dop">Дополнительные сведения</label>
          <textarea name="name" rows="6" cols="30" id="dop"></textarea>
        </div>
        <div class="col-lg-4">
          <label for="#analitica">Аналитика рынка</label>
          <textarea name="name" rows="6" cols="30" id="analitica"></textarea>

        </div>
      </row>
      <row class="pt-3">
        <div class="col-lg-4">
          <label for="#unic">Уникальность вашего проекта</label>
          <textarea name="name" rows="6" cols="30" id="unic"></textarea>
        </div>
        <div class="col-lg-4">
          <label for="#monet">Каналы монетизации</label>
          <textarea name="name" rows="6" cols="30" id="monet"></textarea>

        </div>
        <div class="col-lg-4">
          <label for="#stateg">Стратегия продаж и маркетинг</label>
          <textarea name="name" rows="6" cols="30" id="strateg"></textarea>

        </div>
      </row>
      <div class="container text-center pt-4">
        <h2>Экономические показатели</h2>
      </div>
      <row class="pt-3 justify-content-center">
        <div class="col-lg-4">
          <label for="#resurs">Вложенные ресурсы</label>
          <textarea name="name" rows="6" cols="30" id="resurs"></textarea>
        </div>
        <div class="col-lg-4">
          <label for="#okup">Срок окупаемости (лет)</label>
          <textarea name="name" rows="6" cols="30" id="okup"></textarea>
        </div>
      </row>
    </div>
    <a href="#top" id="scrl"></a>

  </div>
</template>

<script>
import Title from './ru/Title'
import { Btn, Row,Card, } from 'mdbvue'
import { Dropdown, DropdownItem, DropdownToggle,DropdownMenu } from 'mdbvue'

export default {
  name: 'SetProjects',
  components: {
    Title,
    Btn,
    Row,
    Card,
    Dropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem,

  },
  data () {
    return {
    }
  },
  mounted: function () {
  setTimeout(function() {
    var t = $('#top')
    return $("body, html").animate({
        scrollTop: t
    }, 2e3), !1
  }, 1000);
  },
  methods: {
    returnHome (){
        this.$router.push({ name: 'HomeRu'})
    }
  },
}
</script>

<style lang="css">
.logotip__container_set {
  height: 170px;
  padding-left: 25px;
  padding-right: 25px;
  padding-bottom: 25px;
  padding-top: 25px;
}
.dropdown__otrasli {
  padding: 0;
  margin: 0;
}
</style>



